function Search()
{
  const [posts,setPosts] = useState([
    {
      title: 'Post 1',
      description: 'This is the first post',
      date: '2021-08-01',
      tags: ['Computer Science Department','College of Engineering and Technology','Programming Course'],
      upVotes:20,
      identification: 'g783184281'
    },
    {
      title: 'Post 2',
      description: 'This is the second post',
      date: '2021-08-02',
      tags: ['College of Agriculture','Computers','Paper'],
      upVotes:20,
      identification: 'g783184282'
    }
    ,
    {
      title: 'Post 3',
      description: 'This is the third post',
      date: '2021-08-03',
      tags: ['tag1','tag2','tag3'],
      upVotes:20,
      identification: 'g783184283'
    }
  ]);
  const postBoxes = posts.map((post) => {
    if(posts.length==0|| post==null || post==undefined)
    {
      return (
        <div>
          <h1>No Posts Found</h1>
        </div>
      )
    }
    var postTags = undefined;
    if(post.tags!=null || post.tags!=undefined)
    {
      postTags = post.tags.map((tag) => {
        
      return (
        <Grid item xs={6}>
          <Tooltip enterTouchDelay={0} title={<h2 style={{ color: "lightblue" }}>{tag}</h2>} placement="top">
            <Chip sx ={{padding:'0px',width:'fit-content',height:'20vh'}} label={(tag==null) ? 'No Tags Found':tag} style={postStyle}></Chip>
          </Tooltip>
        </Grid>
      )});
    }
    else
    {
      postTags = <Grid item xs={6}>
        <Tooltip enterTouchDelay={0} title={<h2 style={{ color: "lightblue" }}>No Tags Found</h2>} placement="top">
          <Chip sx ={{padding:'0px',width:'fit-content',height:'20vh'}} label={'No Tags Found'} style={postStyle}></Chip>
        </Tooltip>
      </Grid>
    }
     return (
       <div>
        <br></br>
       <Paper elevation={3} sx={{position:'relative',height:'fit-content',width:'55vw',bgcolor:'rgba(25, 58, 111,0.6)',color:'white',padding:'2vh',fontSize:'3vh',border:'1px solid black',borderRadius:'12px'}}>
             <Grid container spacing={2}>
               {postTags}
               <Grid item xs={12}>
                 <h4 className="postTitle">{post.title}</h4>
               </Grid>
               <Grid item xs={12}>
                 <h6>{post.description}</h6>
               </Grid>
               <Grid item xs={6}>
                 <h5>Posted on: {post.date}</h5>
               </Grid>
               <Grid item xs={6}>
                 <h5>Votes: {post.upVotes}</h5>
               </Grid>
               <Grid item xs={6}>
                 <h5>ID: {post.identification}</h5>
               </Grid>
               <Grid item xs={6}>
                 <ThumbUpIcon></ThumbUpIcon>
               </Grid>
             </Grid>      
           </Paper>
           <br></br>
           </div>
     )});
  const [query,setQuery] = useState(' ');
  const [category,setCategory] = useState('title');
  const[realPosts, setRealPosts] = useState([]);
  const documentInput = (q,c) => 
  {
    setQuery(q);
    setCategory(c);
    console.log(query);
    console.log(category);
  } 
  const searchPosts = () =>
  {
    fetch("https://priam-connections.netlify.app/.netlify/functions/search",
    {
      method:'POST',
      headers: {
        'Content-Type': 'text/plain'
      },
      body:JSON.stringify({
        query:query
      })
    })
    .then (res => res.json())
    .then((data)=> 
    {
      setPosts(data);
    })
  };
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const navigate = useNavigate();
  const [selected, setSelected] = useState(0);
  const handleChange = (event) => 
  {
    setCategory(event.target.value);
    event.target.label = event.target.value;
  }
  return (
    <Auth0Provider
    domain="kingtechnician.us.auth0.com"
    clientId="xTbGmROcQrGne2nH7AAEDu1C3E8hKeBW"
    >
    <div className = "App">
      <header className = "homePage App-header">
        <br></br>
        <div className ="searchBox">
          <TextField value={query} sx= {{align:'left',width:'65vw',input:{color:'black'}}} id="search" label="Search" variant="filled" onChange={(e) => documentInput(e.target.value,category)} />
          <Select sx={{width:'20vw',label:{color:'black'}}} labelId="demo-simple-select-label" id="demo-simple-select" value={category} label="Category" onChange={handleChange}>
            <MenuItem value={'title'}>Title</MenuItem>
            <MenuItem value={'description'}>Description</MenuItem>
          </Select>
        </div>
        <br></br>
        <Button variant="contained" onClick={searchPosts}>Search</Button>
        <br></br>
        {postBoxes}
        <SpinnerMenu/>
        <Profile style={{display:'none'}}/>
      </header>
    </div>
    </Auth0Provider>
  )
}

